let _api = "";
if (config.acid != -1) {
	let siteroot = config.siteroot.substr(0, siteinfo.siteroot.indexOf("app/index.php")); //
	_api = siteroot + "addons/kejia_travel/travel/core/web/index.php?_acid=" + config.acid + "&r=";
} else {
	_api = config.domain + 'index.php?s=';
}
// let _api_root = _api+'/api'
const x = {}

x.dev = true;










   x._api_root  = _api




x._img_root = config.domain;
// 
x.ajax = function(url, data, success, method, loading,uId) {
	let uid = x.getStorage('kejia_order_uid');
	let token = x.getStorage('kejia_order_token');
	let openId = x.getStorage('kejia_order_openId');
	data.version = config.version;
	//data.openid=openId;
	if (loading) {
      uni.showLoading({
      	title:""
      })
	} 
	if(!uId){
		data.uid = uid;
	}
	data.token = token;
	data.uniacid = config.uniacid;
	console.log(data)
	// uni.showNavigationBarLoading()
	method = method === 'post' || method === 'POST' ? 'POST' : 'GET'
	uni.request({
		url: x._api_root + url,
		method: method,
		sslVerify: false,
		header: method === 'post' || method === 'POST' ? {
			'content-type': 'application/x-www-form-urlencoded'
		} : {
			'content-type': 'application/json'
		},
		data: data,
		success: function(res) {
			// console.log('',res)
		    uni.hideLoading()
			var ret = res.data
			let code = parseInt(ret.code)
			if (code == 0 || code == 2 || code == 3) {
				'function' === typeof success && success(ret)
			} else if (code == 1) {
				if (ret.msg == '') {
					x.alert('')
				} else {
					x.alert(ret.msg);
				}
			} else if (code == -99) {

				uni.navigateTo({
					url: '/pages/login/login'
				});








			} else {
				console.log(res)
				x.alert('')
			}
		},
		fail: function(err) {
			// x.dev && console.log(err);
			// x.alert('');
		},
		complete: function(res) {
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh();
			switch (res.statusCode) {
				case 408:
					x.toast('~')
					break;
				default:
					break;
			}
			if (x.dev) {
				// console.log(method+':'+x._api_root+url+''+res.statusCode)
			}
		}
	})
}

