x.modelShow = function modelShow(
	title = '',
	content = '?',
	callback = () => {},
	showCancel = true,
	cancelText = '',
	confirmText = ''
) {
	uni.showModal({
		title: title,
		content: content,
		showCancel: showCancel,
		cancelText: cancelText,
		confirmText: confirmText,
		cancelText: cancelText,
		success: function(res) {
			if (res.confirm) {
				// 
				setTimeout(() => {
					callback()
				}, 500)
			} else if (res.cancel) {
				// 
			}
		}
	})
}
//alert
x.alert = function(msg, title, cb) {
	title = title || ''
	uni.showModal({
		title: title,
		content: msg,
		showCancel: false,
		success(res) {
			if (res.confirm) {
				cb && cb();
			}
		}
	})
};

//toast
x.toast = function(msg, icon, duration) {
	icon = icon || 'none';
	duration = duration || 1500;
	uni.showToast({
		title: msg,
		icon: icon,
		duration: duration
	});
};

//
x.successToShow = function successToShow(msg = '', callback = function() {}) {
	setTimeout(function() {
		uni.showToast({
			title: msg,
			icon: 'success',
			duration: 1000,
			success() {
				setTimeout(function() {
					callback()
				}, 500)
			}
		})
	}, 100)

}

//
x.errorToShow = function errorToShow(msg = '', callback = function() {}) {
	setTimeout(function() {
		uni.showToast({
			title: msg,
			icon: 'none',
			duration: 1500,
			success() {
				setTimeout(function() {
					callback()
				}, 1500)
			}
		})
	}, 100)

}
x.setStorage = function(name, value) {
	uni.setStorageSync(name, value)
}

x.getStorage = function(name) {
	return uni.getStorageSync(name)
}
//
x.del = function del(key, sync = true) {
	try {
		if (sync) {
			return uni.removeStorageSync(key);
		} else {
			uni.removeStorage({
				key: key
			});
		}
	} catch (e) {
		return false;
	}
}
x.format = function(timestr) {
	let d = new Date(timestr * 1000)
	let yy = d.getFullYear()
	let mm = d.getMonth() + 1
	mm = mm < 10 ? '0' + mm : mm
	let dd = d.getDate()
	dd = dd < 10 ? '0' + dd : dd;
	let h = d.getHours();
	h = h < 10 ? '0' + h : h;
	let m = d.getMinutes();
	m = m < 10 ? '0' + m : m;
	let s = d.getSeconds();
	s = s < 10 ? '0' + s : s;
	return yy + '-' + mm + '-' + dd + ' ' + h + ':' + m + ':' + s
}

x.format2 = function(timestr) {
	let d = new Date(timestr)
	let yy = d.getFullYear()
	let mm = d.getMonth() + 1
	mm = mm < 10 ? '0' + mm : mm
	let dd = d.getDate()
	dd = dd < 10 ? '0' + dd : dd
	return yy + '-' + mm + '-' + dd
}

x.getParam = function(path, name) {
	var reg = new RegExp("(^|\\?|&)" + name + "=([^&]*)(\\s|&|$)", "i");
	if (reg.test(path))
		return unescape(RegExp.$2.replace(/\+/g, " "));
	return "";
}

