import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        hasLogin: false,  //
        uid:"",   //id
		userInfo:{},   // 
		shopId: "44",   //
		hasShop: false,  //
		deskId: "A5",  //
		deskNum:"2",  //
		needScan:false,  //
		userType: 0,    //1 2  0  3 4 
		_phone:0,    //
		_buyList:[],   //
		wx_audit:{},	
		_config:{},  //
		_orderNo:0,   //
		_orderId:0,   //id
		shop_logo:"/static/head.png",
		intr:'',//
		cancle_time:15 ,//
		serviveTiem:3 ,//
		
    }, 
    mutations: {
		//
        login(state, info) {
            state.userInfo = info.userInfo || {};
            state.uid = info.uid || "" ;
			state.hasLogin = true ;
        },
		//
		logout(state) {
		    state.userInfo = {};
			state.uid = "";
		    state.hasLogin = false;
			uni.clearStorageSync(); 
		},
		//
		intoShop(state, shopId) {
			state.shopId = shopId || 0;
			state.hasShop = true ;
			uni.setStorageSync('kejia_order_shopId',shopId)
		},
		
		// 
		intoCancleTime(state, cancle_time){
			state.cancle_time = cancle_time || 15 ;
		},
		
		//
		intoDesk(state, deskId) {
			state.deskId = deskId || "" ;
			uni.setStorageSync('deskId',deskId)
		},
		//
		intoNum(state, deskNum) {
			state.deskNum = deskNum || ""
			uni.setStorageSync('deskNum',deskNum)
		},
		//
		changeNeedScan(state, needScan) {
			state.needScan = needScan
			uni.setStorageSync('needScan',needScan)
		},
		//
		_setPhone(state, phone) {
			state._phone = phone
		},
		// 
		setIntr(state,intr){
			state.intr = intr
		},
		//
		_setUserInfo(state, userInfo) {
			state.userInfo = userInfo || {};
		},
		//
		_addBuyList(state, list) {
			let s=state._buyList;
			let isExist=false;
			for(let i=0,len=s.length;i<len;i++){
				if(s[i].id==list.id){
					s[i]=list;
					isExist=true;
				}
			};
			if(!isExist){
				s.push(list);
			}
			state._buyList=s;
		},
		//
		_removeBuyList(state, list) {
			let s=state._buyList;
			for(let i=0,len=s.length;i<len;i++){
				if(s[i].id==list.id){
					s[i]=list;
				}
			};
			state._buyList=s;
		},
		//
		_clearBuyList(state) {
			state._buyList=[];
		},
		//audit
		_setAudit(state,info) {
			state.wx_audit=info
		},
		//
		_setOrderId(state,info) {
			state._orderId=info.orderId;
			state._orderNo=info.orderNo;
		},
		_clearOrderId(state) {
			state._orderId=0;
			state._orderNo=0;
		},
		//
		_setConfig(state,info) {
			state._config=info || {};
		}
    }
})

export default store